const scientists = [
    { name: "Albert", surname: "Einstein", born: 1879, dead: 1955, id: 1 },
    { name: "Isaac", surname: "Newton", born: 1643, dead: 1727, id: 2 },
    { name: "Galileo", surname: "Galilei", born: 1564, dead: 1642, id: 3 },
    { name: "Marie", surname: "Curie", born: 1867, dead: 1934, id: 4 },
    { name: "Johannes", surname: "Kepler", born: 1571, dead: 1630, id: 5 },
    { name: "Nicolaus", surname: "Copernicus", born: 1473, dead: 1543, id: 6 },
    { name: "Max", surname: "Planck", born: 1858, dead: 1947, id: 7 },
    { name: "Katherine", surname: "Blodgett", born: 1898, dead: 1979, id: 8 },
    { name: "Ada", surname: "Lovelace", born: 1815, dead: 1852, id: 9 },
    { name: "Sarah E.", surname: "Goode", born: 1855, dead: 1905, id: 10 },
    { name: "Lise", surname: "Meitner", born: 1878, dead: 1968, id: 11 },
    { name: "Hanna", surname: "Hammarström", born: 1829, dead: 1909, id: 12 }
];

const scientistCards = document.querySelectorAll(".scientists__element");

const buttons = document.querySelectorAll("button");
buttons.forEach(button => {
    button.addEventListener("click", () => {
        const cards = document.querySelectorAll(".scientists__element");

        cards.forEach(card => {
            card.classList.add("rotate");
            card.addEventListener("e", () => {
                card.classList.remove("rotate");
            });
        });
    });
});

function updateScientistCards(data) {
    scientistCards.forEach((card, index) => {
        if (data[index]) {
            card.innerHTML = `<p>${data[index].name} ${data[index].surname}</p>`;
            card.style.display = "flex";
        } else {
            card.style.display = "none";
        }
    });
}

document.querySelector(".scientists__century").addEventListener("click", () => {
    updateScientistCards(scientists.filter(person => person.born >= 1801 && person.born <= 1900));
});

document.querySelector(".scientists__sort-alpha").addEventListener("click", () => {
    updateScientistCards([...scientists].sort((a, b) => a.surname.localeCompare(b.surname)));
});

document.querySelector(".scientists__sort-age").addEventListener("click", () => {
    updateScientistCards([...scientists].sort((a, b) => {
        const ageA = a.dead - a.born;
        const ageB = b.dead - b.born;
        return ageB - ageA;
    }));
});
document.querySelector(".scientists__latest").addEventListener("click", () => {
    let latestBorn = scientists[0];
    for (const person of scientists) {
        if (person.born > latestBorn.born) {
            latestBorn = person;
        }
    }
    updateScientistCards([latestBorn]);
});
document.querySelector(".scientists__einstein").addEventListener("click", () => {
    const einstein = scientists.find(person => person.name === "Albert" && person.surname === "Einstein");
    if (einstein) {
        alert(`Альберт Ейнштейн народився у ${einstein.born} році.`);
    }
});
document.querySelector(".scientists__surname-c").addEventListener("click", () => {
    updateScientistCards(scientists.filter(person => person.surname.startsWith("C")));
});

document.querySelector(".scientists__delete-a").addEventListener("click", () => {
    updateScientistCards(scientists.filter(person => !person.name.startsWith("A")));
});
document.querySelector(".scientists__longest-shortest").addEventListener("click", () => {
    let longestLived = scientists[0];
    let shortestLived = scientists[0];

    for (const person of scientists) {
        const age = person.dead - person.born;
        if (age > (longestLived.dead - longestLived.born)) {
            longestLived = person;
        }
        if (age < (shortestLived.dead - shortestLived.born)) {
            shortestLived = person;
        }
    }

    alert(`Найдовше прожив ${longestLived.name} ${longestLived.surname} (${longestLived.dead - longestLived.born} років).
    Найменше прожив ${shortestLived.name} ${shortestLived.surname} (${shortestLived.dead - shortestLived.born} років).`);
});
document.querySelector(".scientists__buttons").addEventListener("click", () => {
    updateScientistCards(scientists.filter(person => person.name[0] === person.surname[0]));
});
updateScientistCards(scientists);
